      <h3 class="center hero text-info">Tecnicas para iniciar y mantener una conversacin</h3>
    <p class="lead text-justify"><u>1. Regala una sonrisa</u><br>
      Claudia recomienda iniciar siempre con un semblante agradable, regalar una sonrisa que tambin incluya la mirada: sonrer con boca y ojos, adems de hacer contacto visual.
      
       <br><br>
      
      <u>2. Busca un tema a tus alrededores</u><br>
