      <br>
      Cuando no se conoce a la persona con la que se quiere conversar, es adecuado presentarse pero tambin se puede iniciar haciendo algn comentario referente al contexto que los rodea; por ejemplo, en un evento deportivo, uno artstico, o comentar brevemente nuestra opinin sobre un artista o la obra expuesta, recomienda Claudia.
      
       <br><br>
      
       <u>3. Muestra inters por el otro</u><br>
      Despus de recibir alguna respuesta favorable, hay que tener la cortesa de mostrar inters por el otro, preguntando algo trivial sobre l o ella, algo que no intimide y que haga que la otra persona se sienta cmoda, expres Claudia.
      <br>
      Adems, resalt que se puede seguir conversando sobre lo que se coment al crear el contacto inicial, pero tambin se puede preguntar sobre su nombre, a qu se dedica o sus ocupaciones en su tiempo libre. 
      <br>
      Cuando ya conocemos a nuestro receptor, preguntar cmo se siente despus de saludarlo, generalmente da inicio a una conversacin, agreg.
      
       <br><br>
      
       <u>4. No olvides hablar sobre ti mismo</u><br>
      Habla un poco sobre ti, cuidando de no mencionar aspectos ntimos que pudieran inhibir a la otra persona. 
      <br>
      Puedes hablar sobre algo en general que te guste que vaya acorde al contexto, puede ser sobre deportes, pasatiempos, trabajo, libros, pelculas, comida, amigos o familiares, cuidando de no mencionar detalles sobre terceros. mencion la experta.
      <br>
      Es muy importante siempre respetar la privacidad de quienes mencionas y la tuya propia, aclar.
      
      <br><br>
      
      <u>5. Haz que la conversacin fluya</u><br>
      La conversacin va a fluir en la medida que las personas se sientan cmodas y no invadidas. 
      <br>
      Segn la experta, si no conoces a la persona con quien se est entablando conversacin, no se debe invadir su espacio, guardando una distancia que demuestre respeto y a la vez inters y ser una pltica muy amena..</p>
