    <div class="col-two-thirds card card-content">
      <h3 class="center hero text-info">10 ideas para empezar una conversacin entretenida</h3>
    <p class="lead text-justify"><strong><mark>1. Empieza por lo fcil</mark></strong><br>
      Hay frmulas que son como la Coca-Cola, funcionan seguro. Preguntas como Qu tal? Cmo va el da? o Buenas, cmo ests? aseguran una respuesta tipo: bien, y t?. Esa es la respuesta que buscas. Este estudio demostr que al responder bien esa persona estar ms dispuesta a entablar una conversacin y adoptar una conducta sociable.
      
      Si alguien te responde: la verdad es que estoy bastante mal, corre insensato.
      
       
      <br><br>
       
      
      <strong><mark>2. Aprende a escuchar
      9 de cada 10 personas no escuchan, sino que esperan su turno para poder hablar.</mark></strong><br>
      
      Nuestro instinto nos lleva a revisar experiencias propias similares a las que cuenta nuestro interlocutor.
      Cuando alguien nos dice: El otro da estuve buceando en las Islas inmediatamente pensamos si nosotros lo hemos hecho, dnde y cundo. Y entonces esperamos que acabe de contarlo -sin escuchar demasiado- para poder contar lo nuestro.
      
      Si quieres ser un buen conversador debers apaciguar ese instinto y escuchar atentamente lo que te cuentan. No pienses en lo que puedes aportar t, cntrate en escuchar y saber ms sobre esa persona.
      
      Esa es la base de todo lo que viene a continuacin.
      
       
      <br><br>
       
      
      <strong><mark>3. No necesitas temas de conversacin interesantes, ellos te los darn.</mark></strong><br>
      Una de las preguntas ms frecuentes a la hora de entablar una conversacin es: De qu hablo? No conozco temas interesantes o divertidos!
      
      Es una mala pregunta.
      
      Lo que te deberas preguntar es: Cmo puedo hacer que hablen de ellos mismos?
      
      Varios estudios han demostrado que una de las estrategias ms efectivas para agradar a la gente es escucharles y pedirles que te cuenten ms.
      
      Segn la neurocientfica Diana Tamir y su compaero de Harvard, Jason Mitchell, hablar de nosotros mismos nos proporciona tanto placer como la comida o el dinero. A juzgar por cmo se comporta la gente en Twitter o Facebook dira que incluso ms.
      
      Cmo lo conseguimos?
      
      Mi frase preferida es: ah, s?. Usar esta pregunta despus de una afirmacin de tu interlocutor asegura que siga hablando para aadir ms detalles.
      Tambin puedes usar otras como: cuntame ms o usar preguntas clsicas como: dnde, cundo, con quin hiciste eso, etc.
      
       
      <br><br>
       
      
      <strong><mark>4. No quieras parecer interesante. No funciona.</mark></strong><br>
      Una vez le o decir a alguien que no hay nada menos sexi que alguien intentando ser sexi.
      Lo mismo se puede aplicar para parecer interesante. Estudios demuestran que, al intentarlo, solo logrars parecer ms estpido.
      
      Un tcnica que a menudo se usa para parecer interesante es relacionarte con personajes conocidos. No lo hagas. Este estudio encontr que usar el clsico Yo conozco a Fernando Alonso no te ayudar a hacer amigos sino todo lo contrario.
      
      La paradoja es que sueles parecer ms interesante cuando te interesas por los dems.
      
      Despierta ese genuino inters que tienes dentro por saber ms sobre ellos. Haz preguntas sin miedo pero con cuidado.
      
      Cules son las mejores? Ahora te cuento ms.
      
       
      <br><br>
       
      
      <strong><mark>5. Las preguntas son la chispa que hace arder la gasolina</mark></strong><br>
      Siempre que hagas una pregunta intenta que sea abierta y no se pueda responder con un s o un no, de lo contrario te arriesgas a que suceda eso mismo y se cree un ambiente incmodo.
      
      Las preguntas que ms me gustan para sacar temas de conversacin son las relacionadas con los problemas.
      Todo el mundo tiene problemas y nos encanta hablar de ellos. Es una manera de darnos la importancia que todos buscamos.
      
      Qu problema estis intentando solucionar en el trabajo? Qu problema te han dado esta semana tus hijos? Cul es el vecino que da ms problemas? Cul es el mayor problema de vivir aqu?
      
      Las respuestas a estas preguntas suelen ser quejas que, a menudo, pueden ser compartidas. Y cuando compartes algo se pone en marcha el principio de similitud que es uno de los que ms conexin con las personas genera.
      
       
      <br><br>
       
      
      <strong><mark>6. Busca y seala las similitudes</mark></strong><br>
      Hay decenas de estudios que demuestran lo potente que es la similitud. Nos gustan ms los nombres que se parecen a los nuestros, la gente que apoya a nuestro equipo, o los que viven en nuestra misma ciudad.
      
      La similitud es tan potente que es una de las bases de la persuasin.
      
      Cuando encuentres una fuente de similiud ahonda en ella. Enlazndolo con el ejemplo de antes.
      
      -Qu problema te han dado esta semana tus hijos?
      -Pues mira, al pequeo le ha dado por pintar y nos ha garabateado toda la pared del saln con el boli.
      -jeje, tambin me pas a m. An me acuerdo cmo me enfad. Oye, y qu has hecho? 
      -Pues mira
      
      Una manera de generar similitud es haciendo sentir a tu interlocutor que formis parte del mismo grupo. Puede que los dos nacieseis en la misma ciudad, fueseis al mismo colegio o que ambos sigis a la misma persona en Instagram. Si lo descubres, pronto generars simpata lo que ayudar a mantener una conversacin agradable.
      
       
      <br><br>
       
      
      <strong><mark>7. Elogia con estilo: pide consejo</mark></strong><br>
      Mi vecino Fer es un chaval simptico. Cada vez que te ve elogia algo: Qu bici tan chula, que gorra tan guay, esa barba te queda genial y slo tiene 9 aos! Si eres de elogio fcil como Fer, enhorabuena. Los elogios han demostrado consistentemente su efecto en la motivacin, productividad y buena disposicin de las personas.
      
      Pero la buena noticia es que si no lo eres. tambin puedes forzar los elogios porque, aunque no sean sinceros funcionan!
      Una de las maneras ms indirectas y elegantes de elogiar a alguien consiste en pedirle consejo. 
      
      Cuando le pedimos consejo a alguien estamos asumiendo implcitamente que lo percibimos como un experto del que deseamos aprender.
