      Tu sonrisa es el elemento que tienes ms a mano para parecer ms atractivo y todos queremos hablar con las personas atractivas. No tengas miedo, sonre al decir ese hola, ese qu tal o al compartir una ancdota. La sonrisa es ms importante que el mejor de los temas de conversacin.
      
       
      <br><br>
       
      
      <strong><mark>9. Cuidado con ser t mismo </mark></strong><br>
      No te invitar a mostrar ser alguien que no eres pero no te limites a ser t.
