      Si quieres mantener buenas conversaciones debes de poner un poco de esfuerzo en mostrarte como alguien agradable y abierto. El simple hecho de mostrar inters por el otro har que le gustes ms.
      
      Y si no te apetece esforzarte, siempre puedes hablar con Sheldon.
      
       
      <br><br>
       
      
      <strong><mark>10. No toques temas controvertidos</mark></strong><br>
