      En su libro Quirkology: How We Discover the Big Truths in Small Things, Richard Wiseman habla de cmo sus experimentaciones demostraron que hablar de viajes fue el tema que ms segundas citas provoc en los participantes de sus dinmicas sociales. La mayora de personas hablaron sobre vacaciones y destinos paradisacos y, claro, eso hace que te sientas bien y contento. Y las personas alegres y contentas son ms atractivas para los dems. Y, como te deca, siempre apetece charlar ms con los atractivos.
      
      No te preocupes si no has viajado mucho. Seguro que hay muchos sitios a los que te gustara ir no? Habla de cmo te los imaginas y lo que haras all. Seguro que los conviertes en una gran fuente de conversacin.</p>
